// 
export function detail (state = fromJS({
  isInit: true,
  info: {}
}), { type, payload }) {
  switch (type) {
    case types.TASK_DETAIL_REQUEST:
      return state.merge({
        isInit: true,
        info: {}
      });
    case types.TASK_DETAIL_SUCCESS:
      return state.merge({
        isInit: false,
        info: payload
      });
    case types.TASK_DETAIL_FAILURE:
      return state.merge({
        isInit: false,
        info: {}
      });
    case types.TASKS_START_TIME_SUCCESS: // 
    case types.TASKS_AUTO_INTERRUPT_TIME: // 
      return state.setIn(['info', 'limitTime'], payload);
    default:
      return state;
  }
}

